import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Switch from "@mui/material/Switch";

const dispatchedEvents = [];

const SrsMuiSwitch = function (props, ref) {
  return (
    <Switch
      checked={props.checked}
      color={props.color}
      disabled={props.disabled}
      onChange={props.onChange}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiSwitch component */
`;

const props = [
  {
    name: "checked",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "color",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiSwitch),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
